@import "../../../responsive";

#sub-menu {
  cursor: pointer;
}

#transit {
  transition: 2s;
}

.menu-main {
  position: fixed;
  bottom: 1.8%;
  left: 1.5%;
  z-index: 1;
  background-color: #fff;
}

.menu {
  z-index: 99;
  color: rgb(107, 107, 107) !important;
  display: flex;
  flex-direction: row;
  background-color: #fff;

  a {
    color: rgb(3, 3, 3) !important;
  }
}

.menu-active {
  color: #000;
  font-weight: 900;
}

.about-menu {
  display: inline-block;
  padding-left: 10px;
  padding-bottom: 1px;
  background-color: #fff;
  transition: 2s;
}
.list {
  transition: 0.3s;
  a {
    display: block;
    padding-bottom: 1px;
  }
}

.ham-menu {
  position: relative;
  width: 80px;

  button {
    position: absolute;
    bottom: 1%;
  }
}
#main-menu {
  font-size: 1.8rem;
}

.contact-menu {
  text-align: right;
  font-family: "CaviaDreams";
  transition: 0.1s;
  transform-origin: left;
  background-color: #fff;
}

.menu-item {
  display: inline-grid;
  text-align: right;
  font-family: "CaviaDreams";
  font-size: 1.5em;
  z-index: 1;
  opacity: 1;
  background: #fff;
}

.full-slider {
  position: fixed;
  display: inline-flex;
  margin-top: -30px;
}

.about-menu {
  font-family: "CaviaDreams";
  transition: 0.2s;
}

.work-menu {
  transition: 0.2s;
  padding-bottom: 5px;
  a {
    display: block;
    border: none !important;
    transition: 0.2s;
  }
}

.burger_hamburger-inner__14XT2 {
  border-radius: 0px !important;
  width: 29px !important;
  height: 3.5px !important;
}

.burger_hamburger-inner__14XT2::before {
  content: "";
  width: 29px !important;
  height: 3.5px !important;
  border-radius: 0px !important;
}

.location {
  text-transform: uppercase;
  font-family: "CaviaDreamsBold";
  font-size: 1.8rem;
}

.submenu {
  font-family: "CaviaDreams";
  font-size: 1.4rem;
  padding-top: 0.12em;
  padding-left: 0.8rem;
  text-transform: capitalize;
}
@media all and (min-width: 1696px) {
  .burger_hamburger-inner__14XT2::before {
    height: 4px !important;
  }
}

.burger_hamburger-inner__14XT2::after {
  content: "";
  width: 29px !important;
  height: 3.5px !important;
  border-radius: 0px !important;
}

.copyright {
  display: none;
  cursor: pointer;
}

@media all and (max-width: 1280px) {
  .menu-main {
    width: 100%;
    min-height: 45px;
    bottom: 0;
    left: 0;
    box-shadow: 0px 0px 6px 1px rgb(206, 206, 206);
    z-index: 9999;
  }

  .menu {
    width: 100%;
    min-height: 45px;
    position: relative;
  }
  .copyright {
    display: block;
    font-size: 1.7rem;
    position: absolute;
    right: 4%;
    bottom: 3%;
  }

  .about-menu {
    padding: 0;
    transition: 0.2s !important;
    a {
      padding-bottom: 5px;
    }
  }

  .menu-item {
    width: 100%;
    text-align: center;
    padding-right: 70px;
  }

  .about-menu {
    text-align: center;
    width: 100%;
    padding-left: 0.8em;
  }

  .work-menu {
    text-align: center;
    width: 100%;
    padding-left: 0.8em;
  }
}

#plus-minus {
  font-size: 2.5rem;
}
