@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'CaviaDreams', 'CaviaDreamsBold',
    'CaviaDreamsItalic','CaviaDreamsBoldItalic';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'CaviaDreams','CaviaDreamsBold',
  'CaviaDreamsItalic','CaviaDreamsBoldItalic';
}

html, body, #app, #app>div {
  height: 100vh;
}
* {
  outline: none;
}

.App {
  height: 100vh;
  cursor: url(data:image/png;base64,), auto;
  width: 100%;
  align-items: stretch;
}

#main-canvas {
  z-index: -1;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.content {
  height: 100vh;
  width: 100vw;
  overflow-y: hidden;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

