@import "../../responsive";

body {
  overflow-x: hidden;
}
.footer {
  font-family: "CaviaDreams";
  color: #6c757d;
  text-align: right;
  position: fixed;
  bottom: 1.3%;
  right: 3%;
  font-size: 2rem;
  cursor: pointer;
}

.tooltip {
  border-radius: 0px !important;
  width: 16rem !important;
  top: 1px !important;
}

.tooltip-inner {
  max-width: 100% !important;
}
@media all and (max-width: 1356px) {
  .footer {
    display: none;
  }
}
